import axios from "axios";
import React, { Component } from "react";
import { connect } from "react-redux";
import { Auth, PushPopupAlert } from "../../actions";
import {
  createClaim,
  DocumentClaimUser,
  GetClaimingTypes,
  getMyClaims,
  getMyRejectedApplications,
  getMyRejectedDocuments,
} from "../../actions/claiming";
import {
  getDocumentCategory,
  getDocumentSubCategory,
  getDocumentFields,
} from "../../actions/document";
import { getActiveSessionPlan } from "../../actions/session";
import { getApplicationSessions } from "../../actions/settings";
import { PopupAlertData } from "../Alert/Popup";
import ApplicationFormView from "../../containers/Claimings/Candidate/ApplicationFormView";
import {
  isClaimAllowed,
  ClaimGetDocTitle,
} from "../../containers/Claimings/Candidate/ClaimForm";
import DocFormView from "../../containers/Claimings/Candidate/DocFormView";
import FormApplicationList from "../../containers/Claimings/Candidate/FormApplicationList";
import FormDocumentList from "../../containers/Claimings/Candidate/FormDocumentList";
import { isPlanActive } from "../../containers/SessionSettings/SessionDetails";
import {
  PlansTypeKeys,
  SessionPlanInterface,
  claimApplicationsInterface,
  PlansInterface,
  CandidateClaimInterface,
} from "../../interfaces";
import { ApplicationStates } from "../../interfaces/application";
import { PopupAlertTheme } from "../../interfaces/popupAlert";
import { StoreState } from "../../reducers";
import { ClaimingReducerType } from "../../reducers/claiming";
import { DocumentReducerInterface } from "../../reducers/document";
import { SessionPlanReducerInterface } from "../../reducers/session";
import { isEmptyOrSpaces, capitalizeFirstLetter } from "../../shared/dataTest";
import { API } from "../../utils/api";
import { setAxiosToken } from "../../utils/AxiosToken";
import { errorToText } from "../../utils/errors";

type ClaimFormProps = {
  // FOR SHORTCUT BUTTONS CALL
  // REQUIRED PROPS
  claim_type: PlansTypeKeys;
  file_id: string;
  setFormState: (value: boolean, response?: boolean) => void;
  auth: Auth;
  sessionPlan: SessionPlanReducerInterface;
  claiming: ClaimingReducerType;
  documentReducer: DocumentReducerInterface;
  getActiveSessionPlan: Function;
  createClaim: Function;
  GetClaimingTypes: Function;
  getDocumentCategory: Function;
  getDocumentSubCategory: Function;
  getDocumentFields: Function;
  getMyRejectedApplications: Function;
  getMyRejectedDocuments: Function;
  getMyClaims: Function;
  PushPopupAlert: (alert: PopupAlertData) => void;
};

type ClaimFormState = {
  loading: boolean;
  loadingForm: boolean;
  formError: {
    element: string;
    msg: string;
  } | null;
  thisClaimType: PlansTypeKeys | null;
  thisClaimTypeId: string;
  claimComment: string;
  claimFileId: string;
  chooseClaimFile: boolean;
  viewThisFile: string;
  formType: "DEFAULT" | "PROPS";
  alertError: string | null;
  activeSession: SessionPlanInterface | null;
  found_claim: CandidateClaimInterface | null;
};

class ClaimFormComponent extends Component<ClaimFormProps, ClaimFormState> {
  constructor(props: ClaimFormProps) {
    super(props);

    this.state = {
      loading: true,
      loadingForm: false,
      formError: null,
      thisClaimType: null,
      thisClaimTypeId: "",
      claimComment: "",
      claimFileId: "",
      chooseClaimFile: false,
      viewThisFile: "",
      formType: "DEFAULT",
      alertError: null,
      activeSession: null,
      found_claim: null,
    };
  }

  isDocumentIdValid = (
    documents: DocumentClaimUser[],
    doc_id: string
  ): {
    status: boolean;
    msg: string;
    alert: PopupAlertTheme;
  } => {
    let doc = documents.find((item) => item.doc_id === doc_id);
    if (doc) {
      // CHECK IF DOCUMENT HAS REJECTED STATUS
      if (doc.validated_by.validation_status !== "rejected") {
        return {
          status: false,
          msg: "Only rejected document are allowed for claim",
          alert: PopupAlertTheme.info,
        };
      }
      // CHECK IF DOCUMENT HAS ALREADY CLAIMED ON
      if (
        this.props.claiming.myClaims &&
        this.state.activeSession &&
        isClaimAllowed(
          this.props.claiming.myClaims,
          this.state.activeSession.session_plan_id,
          doc_id
        )
      ) {
        return {
          status: false,
          msg: "This document already in claiming list!",
          alert: PopupAlertTheme.info,
        };
      }
      return { status: true, msg: "", alert: PopupAlertTheme.success };
    } else {
      return {
        status: false,
        msg: "Invalid document id",
        alert: PopupAlertTheme.warning,
      };
    }
    // return {
    //   status: false,
    //   msg: "No Documents, please try again",
    //   alert: PopupAlertTheme.info,
    // };
  };

  isApplicationIdValid = (
    applications: claimApplicationsInterface[],
    app_id: string
  ): {
    status: boolean;
    msg: string;
    alert: PopupAlertTheme;
  } => {
    let app = applications.find((item) => item.application_id === app_id);
    console.log("====================================");
    console.log({ app, app_id });
    console.log("====================================");
    if (app) {
      // CHECK IF DOCUMENT HAS REJECTED STATUS
      if (app.application_status !== ApplicationStates.Rejected) {
        return {
          status: false,
          msg: "Only rejected application are allowed for claim",
          alert: PopupAlertTheme.info,
        };
      }
      // CHECK IF DOCUMENT HAS ALREADY CLAIMED ON
      if (
        this.props.claiming.myClaims &&
        this.state.activeSession &&
        isClaimAllowed(
          this.props.claiming.myClaims,
          this.state.activeSession.session_plan_id,
          app.application_id
        )
      ) {
        return {
          status: false,
          msg: "This application already in claiming list!",
          alert: PopupAlertTheme.info,
        };
      }
      return { status: true, msg: "", alert: PopupAlertTheme.info };
    } else {
      return {
        status: false,
        msg: "Invalid application id",
        alert: PopupAlertTheme.warning,
      };
    }
    // return {
    //   status: false,
    //   msg: "No Application, please try again!",
    //   alert: PopupAlertTheme.info,
    // };
  };

  setLoading = (state: boolean) => {
    this.setState({ loading: state });
  };

  setLoadingForm = (state: boolean) => {
    this.setState({ loadingForm: state });
  };

  setFormError = (
    data: {
      element: string;
      msg: string;
    } | null
  ) => {
    this.setState({ formError: data });
  };

  setAlertError = (data: string | null) => {
    this.setState({ alertError: data });
  };

  setClaimFileId = (data: string) => {
    this.setState({ claimFileId: data });
  };

  setViewThisFile = (data: string) => {
    this.setState({ viewThisFile: data });
  };

  setChooseClaimFile = (data: boolean) => {
    this.setState({ chooseClaimFile: data });
  };

  setClaimComment = (data: string) => {
    this.setState({ claimComment: data });
  };

  isClaimTypeOpen = (
    thisSession: SessionPlanInterface,
    thisType: PlansTypeKeys
  ): boolean => {
    let data: PlansInterface | undefined;
    if (thisType === PlansTypeKeys.APPLICATION) {
      data = thisSession.plans.find(
        (item) => item.type_key === PlansTypeKeys.APPLICATION
      );
      if (data && isPlanActive(thisSession, data) === "OPEN") {
        return true;
      }
    } else if (thisType === PlansTypeKeys.DOCUMENT_VALIDATION) {
      data = thisSession.plans.find(
        (item) => item.type_key === PlansTypeKeys.DOCUMENT_VALIDATION
      );
      if (data && isPlanActive(thisSession, data) === "OPEN") {
        return true;
      }
    } else if (thisType === PlansTypeKeys.PLACEMENT_TRANSFER) {
      data = thisSession.plans.find(
        (item) => item.type_key === PlansTypeKeys.PLACEMENT_TRANSFER
      );
      if (data && isPlanActive(thisSession, data) === "OPEN") {
        return true;
      }
    }
    return false;
  };

  isClaimBtnValid = (thisSession: SessionPlanInterface) => {
    if (this.props.file_id || this.props.claim_type) {
      // CHECK IF CLAIM TYPE && CLAIM FILE PROPS ARE PASSED PROPS
      if (!this.props.claim_type) {
        this.props.PushPopupAlert({
          title: "Claim type is required!",
          theme: PopupAlertTheme.warning,
        });
        this.props.setFormState(false);
        this.setState({
          claimFileId: "",
          thisClaimType: null,
          thisClaimTypeId: "",
          claimComment: "",
          chooseClaimFile: false,
          formType: "DEFAULT",
          viewThisFile: "",
          formError: null,
        });
      } else if (!this.props.file_id) {
        this.props.PushPopupAlert({
          title: "Claim file is required!",
          theme: PopupAlertTheme.warning,
        });
        this.props.setFormState(false);
        this.setState({
          claimFileId: "",
          thisClaimType: null,
          thisClaimTypeId: "",
          claimComment: "",
          chooseClaimFile: false,
          formType: "DEFAULT",
          viewThisFile: "",
          formError: null,
        });
      } else if (!this.isClaimTypeOpen(thisSession, this.props.claim_type)) {
        // IF CLAIM TYPE && CLAIM FILE PROPS ARE PASSED PROPS
        // CHECK IF CLAIM TYPE IS OPEN ================
        this.props.PushPopupAlert({
          title: "Claim type is closed!",
          theme: PopupAlertTheme.warning,
        });
        this.props.setFormState(false);
        this.setState({
          claimFileId: "",
          thisClaimType: null,
          thisClaimTypeId: "",
          claimComment: "",
          chooseClaimFile: false,
          formType: "DEFAULT",
          viewThisFile: "",
          formError: null,
        });
      }
    }
  };

  displayClaimType = (claimType: PlansTypeKeys): string => {
    let result: string = "";
    switch (claimType) {
      case PlansTypeKeys.APPLICATION:
        result = "application";
        break;
      case PlansTypeKeys.DOCUMENT_VALIDATION:
        result = "document";
        break;
      case PlansTypeKeys.EXAMINATION:
        result = "exam";
        break;
      case PlansTypeKeys.MARKS_PUBLICATION:
        result = "marks publication";
        break;
      case PlansTypeKeys.PLACEMENT_TRANSFER:
        result = "placement";
        break;
      default:
        result = "";
        break;
    }

    return result;
  };

  isClaimsAllowed = (
    thisPeriod: SessionPlanInterface
  ): {
    status: boolean;
    msg: string;
    alert: PopupAlertTheme;
  } => {
    // LOADING CLAIMING OF CURRENT CANDIDATES ================================
    if (
      this.props.claiming.myClaims === null &&
      this.props.file_id &&
      this.props.claim_type
    ) {
      this.props.getMyClaims(
        this.props.auth.user.user_id,
        thisPeriod.session_plan_id,
        (status: boolean, msg: string, data?: CandidateClaimInterface[]) => {
          // !status && setLoadingClaims(status);
          this.setLoading(status);
          if (!status && msg.length) {
            this.props.PushPopupAlert({
              title: msg,
              theme: PopupAlertTheme.danger,
            });
          }
          if (data) {
            let claim = data.find(
              (item) =>
                item.claiming_on_id === this.props.file_id &&
                item.period_id === thisPeriod.session_plan_id
            );
            if (claim) {
              this.setState({ found_claim: claim });
              return {
                status: true,
                msg: `You have already claimed on this ${this.displayClaimType(
                  this.props.claim_type!
                )}`,
                alert: PopupAlertTheme.info,
              };
            }
          }
        }
      );
    } else if (
      this.props.claiming.myClaims &&
      this.props.file_id &&
      this.props.claim_type
    ) {
      let claim = this.props.claiming.myClaims.find(
        (item) =>
          item.claiming_on_id === this.props.file_id &&
          item.period_id === thisPeriod.session_plan_id
      );
      if (claim) {
        this.setState({ found_claim: claim });

        return {
          status: true,
          msg: `You have already claimed on this ${this.displayClaimType(
            this.props.claim_type!
          )}`,
          alert: PopupAlertTheme.info,
        };
      }
    }

    return {
      status: true,
      msg: "",
      alert: PopupAlertTheme.success,
    };
  };

  componentDidMount = () => {
    if (
      this.props.sessionPlan.activeSessionPlan === null ||
      this.props.sessionPlan.activeSessionPlan === "none"
    ) {
      this.props.getActiveSessionPlan(
        (status: boolean, msg: string, data: SessionPlanInterface | "none") => {
          this.setLoading(status);
          if (!status && msg.length) {
            this.props.PushPopupAlert({
              title: msg,
              theme: PopupAlertTheme.danger,
            });
          }
          if (data && data === "none") {
            this.props.PushPopupAlert({
              title: "No session active session",
              theme: PopupAlertTheme.danger,
            });
            this.props.setFormState(false);
            this.setState({
              claimFileId: "",
              thisClaimType: null,
              thisClaimTypeId: "",
              claimComment: "",
              chooseClaimFile: false,
              formType: "DEFAULT",
              viewThisFile: "",
              formError: null,
            });
          } else if (data) {
            let _isClaimsAllowed = this.isClaimsAllowed(data);
            if (!_isClaimsAllowed.status) {
              this.props.PushPopupAlert({
                title: _isClaimsAllowed.msg,
                theme: _isClaimsAllowed.alert,
              });
            }
            // this.props.setFormState(false);
            this.setState({ activeSession: data });
            this.isClaimBtnValid(data);
          }
        }
      );
    } else {
      this.setState({
        activeSession: this.props.sessionPlan.activeSessionPlan,
      });
      this.isClaimBtnValid(this.props.sessionPlan.activeSessionPlan);
      let _isClaimsAllowed = this.isClaimsAllowed(
        this.props.sessionPlan.activeSessionPlan
      );
      if (!_isClaimsAllowed.status) {
        this.props.PushPopupAlert({
          title: _isClaimsAllowed.msg,
          theme: _isClaimsAllowed.alert,
        });
        this.props.setFormState(false);
      }
      this.setLoading(false);
    }

    if (this.props.documentReducer.documentCategory === null) {
      this.props.getDocumentCategory();
    }
    if (this.props.documentReducer.documentSubCategory === null) {
      this.props.getDocumentSubCategory();
    }
    if (this.props.documentReducer.documentFields === null) {
      this.props.getDocumentFields();
    }

    if (this.props.claiming.candidateApplications === null) {
      this.props.getMyRejectedApplications(
        this.props.auth.user.user_id,
        (status: boolean, msg: string, data?: claimApplicationsInterface[]) => {
          this.setLoading(status);
          if (!status) {
            if (msg.length > 0) {
              this.props.PushPopupAlert({
                title: msg,
                theme: PopupAlertTheme.danger,
              });
            }
            if (data) {
              // PROPS VALIDATION PROCESS ###############
              if (
                this.props.claim_type &&
                this.props.file_id &&
                this.state.formType === "DEFAULT"
              ) {
                if (this.props.claim_type === PlansTypeKeys.APPLICATION) {
                  let _isApplicationIdValid = this.isApplicationIdValid(
                    data,
                    this.props.file_id
                  );
                  if (!_isApplicationIdValid.status) {
                    this.props.PushPopupAlert({
                      title: _isApplicationIdValid.msg,
                      theme: _isApplicationIdValid.alert,
                    });
                    this.props.setFormState(false);
                  } else {
                    this.setState({
                      thisClaimType: this.props.claim_type,
                      claimFileId: this.props.file_id,
                      formType: "PROPS",
                    });
                  }
                }
              }
            }
          }
        }
      );
    }

    if (this.props.claiming.candidateDocuments === null) {
      this.props.getMyRejectedDocuments(
        (status: boolean, msg: string, data?: DocumentClaimUser[]) => {
          this.setLoading(status);
          if (!status) {
            if (msg.length > 0) {
              this.props.PushPopupAlert({
                title: msg,
                theme: PopupAlertTheme.danger,
              });
            }
            if (data) {
              // PROPS VALIDATION PROCESS ###############
              if (
                this.props.claim_type &&
                this.props.file_id &&
                this.state.formType === "DEFAULT"
              ) {
                if (
                  this.props.claim_type === PlansTypeKeys.DOCUMENT_VALIDATION
                ) {
                  let _isDocumentIdValid = this.isDocumentIdValid(
                    data,
                    this.props.file_id
                  );
                  if (!_isDocumentIdValid.status) {
                    this.props.PushPopupAlert({
                      title: _isDocumentIdValid.msg,
                      theme: _isDocumentIdValid.alert,
                    });
                    // setAlertError(_isDocumentIdValid.msg);
                    this.props.setFormState(false);
                  } else {
                    this.setState({
                      thisClaimType: this.props.claim_type,
                      claimFileId: this.props.file_id,
                      formType: "PROPS",
                    });
                  }
                }
              }
            }
          }
        }
      );
    }

    // PROPS VALIDATION PROCESS ###############
    if (
      this.props.claim_type &&
      this.props.file_id &&
      this.state.formType === "DEFAULT"
    ) {
      this.setState({ thisClaimType: this.props.claim_type });
      if (
        this.props.claim_type === PlansTypeKeys.DOCUMENT_VALIDATION &&
        this.props.claiming.candidateDocuments
      ) {
        let _isDocumentIdValid = this.isDocumentIdValid(
          this.props.claiming.candidateDocuments,
          this.props.file_id
        );
        if (!_isDocumentIdValid.status) {
          this.props.PushPopupAlert({
            title: _isDocumentIdValid.msg,
            theme: _isDocumentIdValid.alert,
          });
          // setAlertError(_isDocumentIdValid.msg);
          this.props.setFormState(false);
        } else {
          this.setState({
            thisClaimType: this.props.claim_type,
            claimFileId: this.props.file_id,
            formType: "PROPS",
          });
        }
      }
      if (
        this.props.claim_type === PlansTypeKeys.APPLICATION &&
        this.props.claiming.candidateApplications
      ) {
        let _isApplicationIdValid = this.isApplicationIdValid(
          this.props.claiming.candidateApplications,
          this.props.file_id
        );
        if (!_isApplicationIdValid.status) {
          this.props.PushPopupAlert({
            title: _isApplicationIdValid.msg,
            theme: _isApplicationIdValid.alert,
          });
          this.props.setFormState(false);
        } else {
          this.setState({
            thisClaimType: this.props.claim_type,
            claimFileId: this.props.file_id,
            formType: "PROPS",
          });
        }
      }
    }
  };

  getDocumentById = (
    documents: DocumentClaimUser[],
    doc_id: string
  ): DocumentClaimUser => {
    return documents.filter((item) => item.doc_id === doc_id)[0];
  };

  getApplicationById = (
    applications: claimApplicationsInterface[],
    application_id: string
  ): claimApplicationsInterface => {
    return applications.filter(
      (item) => item.application_id === application_id
    )[0];
  };

  getValidation = (
    thisId: string
  ): {
    old_reason: string;
    old_status: string;
    old_evaluated_by: string;
  } => {
    let result: {
      old_reason: string;
      old_status: string;
      old_evaluated_by: string;
    } = { old_reason: "", old_status: "", old_evaluated_by: "" };

    if (
      this.state.thisClaimType === PlansTypeKeys.DOCUMENT_VALIDATION &&
      this.props.claiming.candidateDocuments
    ) {
      result = {
        old_reason: this.getDocumentById(
          this.props.claiming.candidateDocuments,
          thisId
        ).validated_by.comment,
        old_status: this.getDocumentById(
          this.props.claiming.candidateDocuments,
          thisId
        ).validated_by.validation_status,
        old_evaluated_by: this.getDocumentById(
          this.props.claiming.candidateDocuments,
          thisId
        ).validated_by.user_id,
      };
    } else if (
      this.state.thisClaimType === PlansTypeKeys.APPLICATION &&
      this.props.claiming.candidateApplications
    ) {
      result = {
        old_reason: this.getApplicationById(
          this.props.claiming.candidateApplications,
          thisId
        ).override_by.comment,
        old_status: this.getApplicationById(
          this.props.claiming.candidateApplications,
          thisId
        ).override_by.application_status,
        old_evaluated_by: this.getApplicationById(
          this.props.claiming.candidateApplications,
          thisId
        ).override_by.user_id,
      };
    }

    return result;
  };

  cleanToClose = () => {
    this.props.setFormState(false);
    this.setState({
      claimFileId: "",
      thisClaimType: null,
      thisClaimTypeId: "",
      claimComment: "",
      chooseClaimFile: false,
      formType: "DEFAULT",
      viewThisFile: "",
      formError: null,
    });
  };

  handleSubmit = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    if (!this.state.thisClaimType) {
      return this.setFormError({
        element: "claim_type",
        msg: "Claim type is required!",
      });
    }
    if (isEmptyOrSpaces(this.state.claimFileId)) {
      return this.setFormError({
        element: "claim_file",
        msg: `Please select ${this.state.thisClaimType.toLocaleLowerCase()} you are claiming on!`,
      });
    }
    if (isEmptyOrSpaces(this.state.claimComment)) {
      return this.setFormError({
        element: "claim_comment",
        msg: `Claim comment is required!`,
      });
    }

    if (!this.state.activeSession) {
      return this.setAlertError(`No active claiming session is available!`);
    }

    this.setLoadingForm(true);
    try {
      setAxiosToken();
      const res = await axios.post<CandidateClaimInterface>(
        `${API.CLAIMING}/user/create`,
        {
          claiming_on_id: this.state.claimFileId,
          type_key: this.state.thisClaimType,
          user: this.props.auth.user.user_id,
          period_id: this.state.activeSession.session_plan_id,
          reason: this.state.claimComment,
          old_reason: this.getValidation(this.state.claimFileId).old_reason,
          old_status: this.getValidation(this.state.claimFileId).old_status,
          old_evaluated_by: this.getValidation(this.state.claimFileId)
            .old_evaluated_by,
        }
      );
      console.log({ createClaim: res.data });
      if (res.status === 201 || res.status === 200) {
        this.props.createClaim(res.data);
        this.props.PushPopupAlert({
          title: "Claim sent successful",
          theme: PopupAlertTheme.success,
        });
        this.cleanToClose();
        this.props.setFormState(false);
      }
    } catch (error) {
      console.error({ error });
      this.props.PushPopupAlert({
        title: errorToText(error),
        theme: PopupAlertTheme.danger,
      });
    }
    this.props.setFormState(false);
    this.setLoadingForm(false);
  };

  selectClaimType = (thisClaim: string) => {
    this.setState({
      thisClaimType: isEmptyOrSpaces(thisClaim)
        ? null
        : (thisClaim as PlansTypeKeys),
    });
  };

  generateChooseFileTitle = (thisClaimType: PlansTypeKeys | null): string => {
    let result: string = "";

    switch (thisClaimType) {
      case PlansTypeKeys.APPLICATION:
        result = `Select application you are claiming on?`;
        break;
      case PlansTypeKeys.DOCUMENT_VALIDATION:
        result = `Select document you are claiming on?`;
        break;
      case PlansTypeKeys.EXAMINATION:
        result = `Select exam you are claiming on?`;
        break;
      case PlansTypeKeys.MARKS_PUBLICATION:
        result = `Select marks publication you are claiming on?`;
        break;
      case PlansTypeKeys.PLACEMENT_TRANSFER:
        result = `Select placement you are claiming on?`;
        break;
      default:
        result = "Select file you are claiming on?";
        break;
    }

    return result;
  };

  render() {
    if (this.state.found_claim !== null) {
      return (
        <div>
          <h2 className="text-center text-2xl font-bold p-3 text-gray-500">
            Claims on this {this.displayClaimType(this.props.claim_type!)}
          </h2>
          {this.state.found_claim.claims.length >= 1 ? (
            <div>
              {this.state.found_claim.claims.map((itm) => (
                <div className="p-2 border rounded mb-2">
                  <div className="p-1 border-b">
                    <span className="text-gray-400">Created date:</span>{" "}
                    <b>{itm.date_created}</b>
                  </div>
                  <div className="p-1 border-b">
                    <span className="text-gray-400">Reason:</span>{" "}
                    <p>{itm.reason}</p>
                  </div>
                  <div className="p-1 border-b">
                    <span className="text-gray-400">Evaluation comment:</span>{" "}
                    <p>{itm.evaluation_comment}</p>
                  </div>
                  <div className="p-1">
                    <span className="text-gray-400 block">decision:</span>{" "}
                    <h3 className="text-xl font-bold">
                      {itm.decision_taken_status}
                    </h3>
                  </div>
                </div>
              ))}
            </div>
          ) : (
            <div>No claims found!</div>
          )}
        </div>
      );
    }

    if (
      this.state.loading ||
      this.state.activeSession === null ||
      (this.props.claiming.myClaims === null &&
        this.props.file_id &&
        this.props.claim_type)
    ) {
      return null;
    }
    return (
      <div className="pb-3">
        {/* CLAIM FORM ============================================== */}
        {this.state.viewThisFile.length <= 0 && (
          <form onSubmit={(e) => this.handleSubmit(e)}>
            <div className="px-6 pb-3">
              {this.state.alertError && (
                <div
                  className="alert-warning cursor-pointer"
                  onClick={() => this.setAlertError(null)}
                >
                  {this.state.alertError}
                </div>
              )}
              <div className="text-base text-gray-700 sm:text-lg">
                {/* SELECT CLAIM TYPE ============================================== */}
                <div className="flex flex-col mb-2">
                  <label>Claim type</label>
                  <select
                    className={`rounded px-2 py-2 w-full bg-white focus:outline-none ${
                      this.state.formType === "PROPS" && "cursor-not-allowed"
                    } ${
                      this.state.formError &&
                      this.state.formError.element === "claim_type"
                        ? "border-red-500"
                        : "border-gray-500"
                    } border-2`}
                    onChange={(e: React.ChangeEvent<HTMLSelectElement>) => {
                      this.selectClaimType(e.target.value);
                      this.setClaimFileId("");
                      this.state.formError &&
                        this.state.formError.element === "claim_type" &&
                        this.setFormError(null);
                    }}
                    value={
                      this.state.thisClaimType ? this.state.thisClaimType : ""
                    }
                    disabled={
                      this.state.loadingForm || this.state.formType === "PROPS"
                    }
                  >
                    <option value="">Choose claim type</option>
                    {Object.values(PlansTypeKeys).map((item, index) => (
                      <option value={item} key={index + item}>
                        {capitalizeFirstLetter(
                          item.toLocaleLowerCase().replace(/_/g, " ")
                        )}
                      </option>
                    ))}
                  </select>
                  {this.state.formError &&
                    this.state.formError.element === "claim_type" && (
                      <span className="text-red-500">
                        {this.state.formError.msg}
                      </span>
                    )}
                </div>
                {/* END SELECT CLAIM TYPE ============================================== */}

                {/* SELECT FILE TO CLAIM ON ============================================== */}
                <div className="flex flex-col mb-2">
                  <label>
                    {this.generateChooseFileTitle(this.state.thisClaimType)}
                  </label>
                  <div
                    className={`text-left bg-white border-2 ${
                      this.state.formType !== "PROPS"
                        ? "border-gray-700"
                        : "border-gray-400 text-gray-500"
                    } rounded w-full ${
                      this.state.chooseClaimFile ? "h-64" : "h-10"
                    } ${
                      this.state.formError &&
                      this.state.formError.element === "claim_file"
                        ? "border-red-500"
                        : ""
                    } border-2 ${
                      this.state.thisClaimType &&
                      !this.state.chooseClaimFile &&
                      this.state.formType !== "PROPS"
                        ? "cursor-pointer"
                        : !this.state.chooseClaimFile && "cursor-not-allowed"
                    }`}
                    title={
                      !this.state.thisClaimType
                        ? "Choose claim type first!"
                        : ""
                    }
                    onClick={() => {
                      this.state.formType === "DEFAULT" &&
                        this.state.thisClaimType &&
                        !this.state.chooseClaimFile &&
                        this.setChooseClaimFile(true);
                      this.state.formError &&
                        this.state.formError.element === "claim_file" &&
                        this.setFormError(null);
                    }}
                  >
                    {/* DISPLAYING FILTERED SELECTING FILE ============================ */}
                    {this.state.claimFileId.length > 0 &&
                    !this.state.chooseClaimFile ? (
                      <p className="my-1 mx-2">
                        {this.state.thisClaimType ===
                          PlansTypeKeys.DOCUMENT_VALIDATION &&
                          this.props.claiming.candidateDocuments &&
                          ClaimGetDocTitle(
                            this.props.documentReducer,
                            this.getDocumentById(
                              this.props.claiming.candidateDocuments,
                              this.state.claimFileId
                            )
                          ).main}
                        {this.state.thisClaimType ===
                          PlansTypeKeys.APPLICATION &&
                          this.props.claiming.candidateApplications &&
                          this.getApplicationById(
                            this.props.claiming.candidateApplications,
                            this.state.claimFileId
                          ).position.position_title}
                      </p>
                    ) : (
                      !this.state.chooseClaimFile && (
                        <p className="my-1 mx-2">
                          Select Claim{" "}
                          {this.state.thisClaimType &&
                            this.state.thisClaimType
                              .toLocaleLowerCase()
                              .replace(/_/g, " ")}
                        </p>
                      )
                    )}
                    {/* END DISPLAYING FILTERED SELECTING FILE ============================ */}

                    {/* LISTING OF REJECTED DOCUMENTS ============================ */}
                    {this.state.chooseClaimFile &&
                      this.state.thisClaimType ===
                        PlansTypeKeys.DOCUMENT_VALIDATION &&
                      this.props.claiming.candidateDocuments &&
                      this.props.claiming.myClaims &&
                      this.props.sessionPlan.activeSessionPlan &&
                      this.props.sessionPlan.activeSessionPlan !== "none" && (
                        <FormDocumentList
                          setChooseClaimFile={this.setChooseClaimFile}
                          setClaimFileId={this.setClaimFileId}
                          setViewThisFile={this.setViewThisFile}
                          viewThisFile={this.state.viewThisFile}
                          candidateDocuments={
                            this.props.claiming.candidateDocuments
                          }
                          documentCategories={this.props.documentReducer}
                          myClaims={this.props.claiming.myClaims}
                          activeSession={
                            this.props.sessionPlan.activeSessionPlan
                          }
                        />
                      )}
                    {/* END LISTING OF REJECTED DOCUMENTS ============================ */}
                    {/* LISTING OF REJECTED APPLICATION ============================ */}
                    {this.state.chooseClaimFile &&
                      this.state.thisClaimType === PlansTypeKeys.APPLICATION &&
                      this.props.claiming.candidateApplications &&
                      this.props.claiming.myClaims &&
                      this.props.sessionPlan.activeSessionPlan &&
                      this.props.sessionPlan.activeSessionPlan !== "none" && (
                        <FormApplicationList
                          setChooseClaimFile={this.setChooseClaimFile}
                          setClaimFileId={this.setClaimFileId}
                          setViewThisFile={this.setViewThisFile}
                          viewThisFile={this.state.viewThisFile}
                          candidateApplications={
                            this.props.claiming.candidateApplications
                          }
                          myClaims={this.props.claiming.myClaims}
                          activeSession={
                            this.props.sessionPlan.activeSessionPlan
                          }
                        />
                      )}
                    {/* END LISTING OF REJECTED APPLICATION ============================ */}
                  </div>
                  {this.state.formError &&
                    this.state.formError.element === "claim_file" && (
                      <span className="text-red-500">
                        {this.state.formError.msg}
                      </span>
                    )}
                </div>
                {/* END SELECT FILE TO CLAIM ON ============================================== */}

                {/* CLAIM COMMENT ============================================== */}
                <div className="flex flex-col">
                  <label>Comment</label>
                  <textarea
                    className={`text-left input-white w-20 inline-block ${
                      this.state.formError &&
                      this.state.formError.element === "claim_comment"
                        ? "border-red-500"
                        : "border-gray-500"
                    } border-2`}
                    placeholder={`Claim comment`}
                    style={{ resize: "none" }}
                    value={this.state.claimComment}
                    onChange={(e) => {
                      this.setClaimComment(e.target.value);
                      this.state.formError &&
                        this.state.formError.element === "claim_comment" &&
                        this.setFormError(null);
                    }}
                    rows={3}
                    disabled={this.state.loadingForm}
                  ></textarea>
                  {this.state.formError &&
                    this.state.formError.element === "claim_comment" && (
                      <span className="text-red-500">
                        {this.state.formError.msg}
                      </span>
                    )}
                </div>
                {/* END CLAIM COMMENT ============================================== */}
              </div>
              <div className="pt-3 flex justify-between">
                <button
                  onClick={() => this.cleanToClose()}
                  className={`btn bg-gray-400 text-white hover:bg-gray-600 focus:outline-none ${
                    this.state.loadingForm && "cursor-not-allowed"
                  }`}
                  type="button"
                  disabled={this.state.loadingForm}
                >
                  Exit
                </button>
                <button
                  className={`btn bg-primary text-white hover:bg-blue-600 focus:outline-none ${
                    this.state.loadingForm && "cursor-wait"
                  }`}
                  type="submit"
                  disabled={this.state.loadingForm}
                >
                  {this.state.loadingForm ? "Loading..." : "Claim"}
                </button>
              </div>
            </div>
          </form>
        )}
        {/* END CLAIM FORM ============================================== */}

        {/* CLAIM DOCUMENT VIEW ============================================== */}
        {this.state.viewThisFile.length > 0 &&
          this.props.claiming.candidateDocuments &&
          this.state.thisClaimType === PlansTypeKeys.DOCUMENT_VALIDATION && (
            <DocFormView
              thisDoc={this.getDocumentById(
                this.props.claiming.candidateDocuments,
                this.state.viewThisFile
              )}
              setViewThisFile={this.setViewThisFile}
              documentCategories={this.props.documentReducer}
              setChooseClaimFile={this.setChooseClaimFile}
              setClaimFileId={this.setClaimFileId}
            />
          )}
        {/* END CLAIM DOCUMENT VIEW ============================================== */}
        {/* CLAIM APPLICATION VIEW ============================================== */}
        {this.state.viewThisFile.length > 0 &&
          this.props.claiming.candidateApplications &&
          this.state.thisClaimType === PlansTypeKeys.APPLICATION && (
            <ApplicationFormView
              thisApplication={this.getApplicationById(
                this.props.claiming.candidateApplications,
                this.state.viewThisFile
              )}
              documentCategories={this.props.documentReducer}
              setChooseClaimFile={this.setChooseClaimFile}
              setClaimFileId={this.setClaimFileId}
              setViewThisFile={this.setViewThisFile}
            />
          )}
        {/* END CLAIM APPLICATION VIEW ============================================== */}
      </div>
    );
  }
}

const mapStateToProps = ({
  auth,
  document,
  claiming,
  sessionPlan,
}: StoreState): {
  auth: Auth;
  sessionPlan: SessionPlanReducerInterface;
  documentReducer: DocumentReducerInterface;
  claiming: ClaimingReducerType;
} => {
  return {
    auth: auth,
    sessionPlan,
    documentReducer: document,
    claiming,
  };
};

export default connect(mapStateToProps, {
  getActiveSessionPlan,
  getMyClaims,
  createClaim,
  GetClaimingTypes,
  getMyRejectedApplications,
  getMyRejectedDocuments,
  getDocumentCategory,
  getDocumentSubCategory,
  getDocumentFields,
  getApplicationSessions,
  PushPopupAlert,
})(ClaimFormComponent);
